import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormArray } from '@angular/forms';
import { ProductAttributesService } from '../product-attributes.service';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';

@Component({
  selector: 'app-add-product-attribute',
  templateUrl: './add-product-attribute.component.html',
  styleUrls: ['./add-product-attribute.component.scss'],
})
export class AddProductAttributeComponent implements OnInit {
  image;
  constructor(
    private fb: FormBuilder,
    private productAttributesService: ProductAttributesService,
    private toastr: ToastrService,
    private router: Router
  ) {}

  form = this.fb.group({
    name: this.fb.group({
      en: ['', Validators.required],
      ar: ['', Validators.required],
    }),
    values: this.fb.array([]),
  });

  addValue() {
    const values = this.form.get('values') as FormArray;
    values.push(
      this.fb.group({
        en: ['', Validators.required],
        ar: ['', Validators.required],
      })
    );
  }

  removeValue(valueIndex) {
    const values = this.form.get('values') as FormArray;
    if (values.controls.length > 0) {
      values.removeAt(valueIndex);
    }
  }

  ngOnInit(): void {}
  onSubmit(formValue) {
    const formData = new FormData();
    console.log(formValue);
    for (const key in formValue) {
      if (formValue[key] instanceof File) {
        formData.append(key, formValue[key]);
      }
      if (key === 'name' || key === 'values') {
        // contains object
        formData.append(key, JSON.stringify(formValue[key]));
      }
    }

    this.productAttributesService.addOne(formData).subscribe(
      (res) => {
        console.log(res);
        this.image = null;
        this.form.reset();
        this.toastr.info(res['message'], 'Info');
        this.router.navigate(['../']);
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }
}
